import React from 'react';
import { useContext } from 'react';
import { AppContext } from '../../App';
import DailyLi from './DailyLi';
import Loader from '../compLoader/Loader';
//import $ from 'jquery';
const Daily = () => {

  const { _weatherData } = useContext(AppContext)


  return (
    <section className='comp-daily'>
      <div className="section-inner">
          <h2>Daily</h2>
          <ul>
            {
              (_weatherData) ? _weatherData.daily.map(v => <DailyLi daliyData={v} key={v.dt} />) : <Loader />
            }
          </ul>
      </div>
    </section >
  );
};

export default Daily;