import React, { useContext } from 'react';
import { AppContext } from '../../App';
import { fnSetWeatherInfo } from '../../js/weather';
import Svg from './Svg';

const CurrentOutput = () => {
  const { _weatherData, _addressKo,_addressEn, fnAppInit } = useContext(AppContext)

  const { temp, description, icon, bg, windSpeed, windDeg, humidity, rain, uvi, uviDesc, feel, sunrise, sunset, date, time, day, apm
    , snow } = fnSetWeatherInfo(_weatherData.current, _weatherData.timezone)
  const fnResetHandler = () => {

    let result = window.confirm(`현재 날씨정보를 갱신하시겠습니까?`)
    if (result) {
      fnAppInit()
    }
  }

  return (
    <div className='current-output' style={{ backgroundImage: `url(${process.env.PUBLIC_URL}/img/weather/${bg}.jpg)` }}>

      <button onClick={fnResetHandler} className='reset'>
        <i className="fa-solid fa-arrows-rotate"></i>
      </button>

      <address>
        <span>[{_addressKo}{_addressEn}]</span>
        <span>[{_addressKo}{_addressEn}]</span>
      </address>
      <div className='circle-container'>
        <div className="sunset">
          <Svg currentData={_weatherData.current} />
          <time className='rise-time'>
            {sunrise}
          </time>

          <time className='set-time'>
            {sunset}
          </time>
        </div>
        <div className='circle-container-inner'>
          <p className='temp'>
            <em>{temp}<sup>&#176;c</sup></em>
            <img src={`${process.env.PUBLIC_URL}/img/icons/${icon}.gif`} alt="" />
            <span>
              <i>Feel</i>
              <b>{feel}<sup>&#176;c</sup></b>
            </span>
          </p>
          <p className='desc'><i className="fa-solid fa-pen"></i>{description}</p>
          <p className='etc'>
            <span className='wind'>
              <i style={{ transform: `rotate(${windDeg - 45}deg)` }} className="fa-solid fa-location-arrow"></i>
              <i className="fa-solid fa-wind"></i>
              <b>{windSpeed}</b>
              <small>m/s</small>
            </span>
            <span className='humidity'>
              <i className="fa-solid fa-droplet"></i>
              <b>{humidity}</b>
              <small>%</small>
            </span>
            <span className='uvi'>
              <i className="fa-regular fa-sun"></i>
              <b>{uvi}</b>
              <small>({uviDesc})</small>
            </span>
            {
              (rain!==0) &&
              <span className='rain'>
                <i className="fa-solid fa-umbrella"></i>
                <b>{rain}</b>
                <small>mm</small>
              </span>
            }
            {
              (snow!==0) &&
              <span>
                <i className="fa-solid fa-snowflake"></i>
                <b>{snow}</b>
                <small>mm</small>
              </span>
            }
          </p>
          <p className='time-date'>
            <time className='time'><small>{apm}</small> {time}</time>
            <time className='date'>{date} ({day})</time>
          </p>
        </div>
      </div>

    </div>
  );
};

export default CurrentOutput;