import React from 'react';
import $ from 'jquery';
import { fnSetWeatherInfo } from '../../js/weather';
import { useContext } from 'react';
import { AppContext } from '../../App';

const DailyLi = ({ daliyData }) => {
  const { _weatherData } = useContext(AppContext)
  const { mmdd, day, description, icon, tempMin, tempMax, rain, snow, feel, clouds, humidity, pressure, uvi, uviDesc, windDeg, windSpeed, windName } = fnSetWeatherInfo(daliyData, _weatherData.timezone)

const fnAccordion = function(e){
  e.preventDefault()
  $(`.comp-daily li .detail`).stop().slideUp()
  $(e.currentTarget).siblings('.detail').stop().slideToggle()
  $(`.comp-daily li a`).not(e.currentTarget).removeClass(`active`)
  $(e.currentTarget).toggleClass(`active`)
}

  return (
    <li>
      <a href='#' onClick={fnAccordion}>
        <p className='date'>
          <span><i className="fa-solid fa-calendar-days"></i>{mmdd} ({day})</span>
          <span className='desc'>'{description}'</span>
        </p>
        <p className='info'>
          <img src={`${process.env.PUBLIC_URL}/img/icons/${icon}.gif`} alt="" />
          <span className='temp'>
            <i className="fa-solid fa-temperature-three-quarters"></i>
            <em>{tempMin}<sup>&#176;</sup>c / {tempMax}<sup>&#176;</sup>c</em>
          </span>
          {
            (rain !== 0) &&
            <span>
              <i className="fa-solid fa-umbrella"></i>
              <em>{rain}mm</em>
            </span>
          }
          {
            (snow !== 0) &&
            <span>
              <i className="fa-solid fa-snowflake"></i>
              <em>{snow}mm</em>
            </span>
          }
        </p>
        <i className="fa-sharp fa-solid fa-arrow-down"></i>
      </a>
      <div className="detail">
        <p>
          <span>
            <i className="fa-solid fa-person-rays"></i>
            <b>체감온도</b>
            <em>{feel}<small>&#176;</small>c</em>
          </span>
        </p>
        <p>
          <span>
            <i className="fa-solid fa-location-arrow" style={{ transform: `rotate(${windDeg - 45}deg)` }}></i>
            <b>풍향({windName})</b>
          </span>
          <span>
            <i className="fa-solid fa-wind"></i>
            <b>풍속</b>
            <em>{windSpeed}<small>m/s</small></em>
          </span>
        </p>
        <p>
          <span>
            <i className="fa-solid fa-cloud"></i>
            <b>구름양</b>
            <em>{clouds}%</em>
          </span>
        </p>
        <p>
          <span>
            <i className="fa-solid fa-droplet"></i>
            <b>습도</b>
            <em>{humidity}%</em>
          </span>
        </p>
        <p>
          <span>
            <i className="fa-solid fa-arrows-to-circle"></i>
            <b>기압</b>
            <em>{pressure}hPa</em>
          </span>
        </p>
        <p>
          <span>
            <i className="fa-regular fa-sun"></i>
            <b>자외선</b>
            <em>{uvi},({uviDesc})</em>
          </span>
        </p>
      </div>
    </li>
  );
};
//{humidity} {pressure}
export default DailyLi;