import React, { useContext } from 'react';
import { AppContext } from '../../App';
import Loader from '../compLoader/Loader';
import HourlyLi from './HourlyLi';
import 'chart.js/auto';
import { Chart } from 'react-chartjs-2';
import ChartDataLabels from 'chartjs-plugin-datalabels';

const Hourly = () => {
  const { _weatherData } = useContext(AppContext)
  let hourlyData
  let chartArr //chartData datasets data 값
  let chartData // chart data속성값
  let chartOptions //chart 엘리먼트 options 속성값

  if (_weatherData) {
    hourlyData = _weatherData.hourly
    chartArr = hourlyData.map(v => parseFloat((v.temp - 273.15).toFixed(1)))
    chartData = {
      labels: new Array(48).fill(''),
      datasets: [{
        label: 'myGraph',
        data: chartArr,
        borderWidth: 3,
        fill: false,
        borderColor: '#41759e',
        tension: 0.5,
      }]
    }
    chartOptions = {
      scales: {
        x: {
          display: false,
        },
        y: {
          display: true,
          beginAtZero: true,
          min: -30,
          max: 50,
          stepSize: 1,
          border: {
            display: true,
            color: 'rgba(255,0,0,0.4)',
            width: 3,
            dash: [2, 2],
          },
          grid: {
            color: 'orange'
          },
          ticks: {
            display: true,
            color: 'orange',
            stepSize: 10,
          },
        },
      },
      plugins: {
        legend: {
          display: false,//수정
          position: 'top',
          labels: {
            color: '#FFF'
          }
        },//legend
        datalabels: {
          display: true,
          color: "#41759e",
          align: 'top',
        },
      },//plguins
      responsive: true,
      maintainAspectRatio: false,
    }
  }




  return (
    <section className="comp-hourly">
      <div className="section-inner">
        <h2>hourly</h2>
        {
          (_weatherData) ?
            <div className="wrap">
              <ul>
                {
                  hourlyData.map(v => <HourlyLi key={v.dt} data={v} />)
                }
              </ul>
              <div className="chart-wrap">
                <Chart type='line' data={chartData} options={chartOptions} plugins={[ChartDataLabels]} />
              </div>
            </div>
            :
            <Loader />
        }
      </div>
    </section>
  );
};

export default Hourly;