import moment from "moment-timezone"
import { codeEnArr, codeKrArr } from "./conditionCode"
export const fnGetWeatherData = (latLngObj) => {
  return new Promise((resolve) => {
    const { lat, lng } = latLngObj
    let url = `https://api.openweathermap.org/data/3.0/onecall?lat=${lat}&lon=${lng}&appid=${process.env.REACT_APP_OPEN_WEATHER_KEY}`
    fetch(url)
      .then((data) => {
        resolve(data.json())
      })
      .catch(() => {
        alert(`정보를 받아오는 중 오류가 발생했습니다`)
      })
  })
}//weatherData 출력


export const fnSetWeatherInfo = (weatherDataObj, timezone) => {
  let sunset
  let sunrise
  if (weatherDataObj.sunrise) {
    sunrise = moment(weatherDataObj.sunrise * 1000).tz(timezone).format('a hh:mm:ss')
    sunset = moment(weatherDataObj.sunset * 1000).tz(timezone).format('a hh:mm:ss')
  }
  else {
    sunrise = '-- : -- : --'
    sunset = '-- : -- : --'
  }//invali data 입력 취소


  let apm = moment(weatherDataObj.dt * 1000).tz(timezone).format('a')
  let date = moment(weatherDataObj.dt * 1000).tz(timezone).format(' YYYY년 M월 D일')
  let time = moment(weatherDataObj.dt * 1000).tz(timezone).format('hh:mm:ss')
  let hhmm = moment(weatherDataObj.dt * 1000).tz(timezone).format('HH:mm')
  let dayArr = [`일`, `월`, `화`, `수`, `목`, `금`, `토`]
  let day = dayArr[moment(weatherDataObj.dt * 1000).tz(timezone).day()]
  let mmdd = moment(weatherDataObj.dt * 1000).tz(timezone).format('M월 D일')



  let temp; let tempMin; let tempMax; let tempMorning; let tempDay; let tempNight

  if (typeof weatherDataObj.temp === 'object') {
    tempMin = (weatherDataObj.temp.min - 273.15).toFixed(1)
    tempMax = (weatherDataObj.temp.max - 273.15).toFixed(1)
    tempMorning = (weatherDataObj.temp.morn - 273.15).toFixed(1)
    tempDay = (weatherDataObj.temp.day - 273.15).toFixed(1)
    tempNight = (weatherDataObj.temp.night - 273.15).toFixed(1)


  }//object
  else {
    temp = (weatherDataObj.temp - 273.15).toFixed(1)
  }//number
  let icon = weatherDataObj.weather[0].icon
  let bg = weatherDataObj.weather[0].main
  let description = weatherDataObj.weather[0].description
  description = codeKrArr[(codeEnArr.indexOf(description))]
  let windDeg = weatherDataObj.wind_deg
  let windName
  if(windDeg-45===0){
    windName='남풍'
  }
  else if(0<(windDeg-45)&&(windDeg-45)<90){
    windName='남서풍'
  }
  else if(windDeg-45===90){
    windName='서풍'
  }
  else if(90<(windDeg-45)&&(windDeg-45)<180){
    windName='북서풍'
  }
  else if(windDeg-45===180){
    windName='북풍'
  }
  else if(180<(windDeg-45)&&(windDeg-45)<270){
    windName='북동풍'
  }
  else if(windDeg-45===270){
    windName='동풍'
  }
  else if(270<(windDeg-45)&&(windDeg-45)<360){
    windName='남동풍'
  }
  let windSpeed = weatherDataObj.wind_speed
  let humidity = weatherDataObj.humidity
  let rain = (weatherDataObj.rain) ? (typeof weatherDataObj.rain === 'object') ? weatherDataObj.rain[`1h`] : weatherDataObj.rain : 0
  let snow = (weatherDataObj.snow) ? (typeof weatherDataObj.snow === 'object') ? weatherDataObj.snow[`1h`] : weatherDataObj.snow : 0
  let feel
  if (typeof (weatherDataObj.feels_like) === 'object') {
    feel = (weatherDataObj.feels_like.day - 273.15).toFixed(1)
  }
  else {
    feel = (weatherDataObj.feels_like - 273.15).toFixed(1)
  }


  let uvi = weatherDataObj.uvi
  let uviDesc
  if (uvi < 3) { uviDesc = '낮음' }
  else if (uvi >= 3 && uvi < 6) { uviDesc = '보통' }
  else if (uvi >= 6 && uvi < 8) { uviDesc = '높음' }
  else if (uvi >= 8 && uvi < 11) { uviDesc = '매우높음' }
  else { uviDesc = '위험' }

  let clouds = weatherDataObj.clouds
  let pressure = weatherDataObj.pressure


  return {
    temp, description, icon, bg, windDeg, windSpeed, humidity, rain, uvi, uviDesc, feel, sunrise, sunset, date, time,hhmm, day, apm, mmdd
    , tempMin, tempMax, tempMorning, tempDay, tempNight, snow,clouds,pressure,windName
  }
}//weatherData를 가공






