import { createContext, useLayoutEffect, useState } from "react";
import Current from "./components/compCurrent/Current";
import Daily from "./components/compDaily/Daily";
import Hourly from "./components/compHourly/Hourly";
import Map from "./components/compMap/Map";
import { fnGetAddress, fnGetLatLng } from "./js/map";
import { fnGetWeatherData } from "./js/weather";
//import moment from "moment-timezone"

export const AppContext = createContext()

function App() {
  const [_latLng, _setLatLng] = useState(null)
  const [_weatherData, _setWeatherData] = useState(null)
  const [_addressEn, _setAddressEn] = useState(null)
  const [_addressKo, _setAddressKo] = useState(null)


  const fnAppInit = async () => {
    let latLngObj = await fnGetLatLng()
    _setLatLng(latLngObj)
    let addressKo = await fnGetAddress(latLngObj,'ko')
    _setAddressKo(addressKo)
    let addressEn = await fnGetAddress(latLngObj,'en')
    _setAddressEn(addressEn)
    const weatherData = await fnGetWeatherData(latLngObj)
    _setWeatherData(weatherData)
  }//fnAppInit // 위경도,주소,날씨정보를 초기설정

  useLayoutEffect(() => {
    fnAppInit()
  }, [])

  return (
    <AppContext.Provider value={{ _latLng, _setLatLng, _weatherData, _setWeatherData, _addressEn, _setAddressEn,_addressKo, _setAddressKo, fnAppInit}}>
      <h1><img src={`${process.env.PUBLIC_URL}/img/main//title.png`} alt="" /></h1>
      <div className="app-inner">
        <Current />
        <Map />
        <Hourly />
        <Daily /> 
      </div>
    </AppContext.Provider>
  );
}

export default App;
